import React, { Fragment } from "react";
import { Button } from "antd";
import {
    ModalContent,
    ModalOpenButton,
    useModalContext,
} from "components/ui/Modal";
import { Form, FormContentBase } from "components/ui/Form/Form";
import { AccessControlTypes } from "types";

export default function FolderModal({ parentPath, type }) {
    const { onOk, form } = useModalContext();

    function handleOk() {
        onOk("/folder", "create", {
            path: parentPath ? parentPath += "\\" + form.getFieldValue("name") : form.getFieldValue("name"),
            type
        });
    }

    return (
        <Fragment>
            <ModalOpenButton requiredRoles={["Administrator", "Operator"]} requiredAccessControls={AccessControlTypes.Create} allowedWithOneWayGitSync={false}>
                <Button children="Create New Folder" />
            </ModalOpenButton>
            <ModalContent title="Create new folder" onClickOk={handleOk} formName="create_folder_form">
                <Form schemaName="folder">
                    <FormContentBase
                        name="create_folder_form"
                        form={form}
                        preserve={false}
                    />
                </Form>
            </ModalContent>
        </Fragment>
    );
}
